
.NewMessageContainer {
    @extend #flex-row-center-padding;
}
.NewMessage {
    width: 100%;
    max-width: 800px;
}
.NewEmailHeader {
    width: 100%;
    box-sizing: border-box;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}
.NewEmailHeader span {
    font-size: $lg-font-size;
    font-weight: 500;
}

 
