.ToContainer {
    display: flex;
    align-items: flex-end;
    width: 100%;
}
.ToPlaceholder {
    width: 25px;
    border-width: 0 0 1px 0;
    border-color: #767676;
    border-style: solid;
    font-size: $md-font-size;
    padding-bottom: 2px;
    color: #757575;
}
#editor {
    border: none; 
    font: 400 13.33px Arial;
    font-size: $md-font-size;
    height: 250px;
    resize: none;
}
#editor:focus {
    border: none;
    outline: none;
    overflow: hidden;
}


